    z-index: 40;
  }
}

.header.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 0rem 0.25rem 1.375rem #bbc8e6;
  background: #ffffff !important;
  z-index: 40;
}

.header.open {
  background-color: #f3f4f8 !important;
}

.header.open-sub-nav {
  background-color: #fff !important;
}

@media (max-width: 767px) {
  .header.open-sub-nav {
    background-color: #f3f4f8 !important;
  }
}

.header__container {
