  .header .head-btn {
    display: none;
  }
}

.head-logo {
  display: block;
  max-width: 11rem;
  flex-shrink: 0;
}

.head-logo__img {
  width: 100%;
}

@media (max-width: 767px) {
  .head-nav {
    display: none;
  }
}

.head-nav__list {
  display: flex;
  margin-bottom: 0.3125rem;
}

.head-nav__li {
  margin: 0 0.9375rem;
}

@media (max-width: 767px) {
  .head-nav__li {
    margin: 0;
    margin-bottom: 1.25rem;
  }
}

.head-nav__link {
  font-family: "Futura PT Demi";
  font-size: 1.25rem;
  font-weight: 600;
  font-style: normal;
  line-height: 1.625rem;
  position: relative;
  letter-spacing: .03em;
}

.head-nav__link:hover {
  text-decoration: none;
  opacity: .85;
}

.head-nav__link:after {
  position: absolute;
  bottom: 0rem;
  left: 50%;
  display: block;
  width: 1.4375rem;
  height: 0.3125rem;
  transition: opacity .2s ease;
  transform: translate(-50%, 0);
  opacity: 0;
  background: url("../img/icons/dots.svg") no-repeat 50%/1.4375rem;
}

.head-nav__link.link-sub {
  padding: 1.25rem 0;
}

.head-nav__link.link-sub:after {
  content: "";
  opacity: 1;
}

.head-nav__link.link-sub:before {
  position: absolute;
  bottom: -3.0625rem;
  left: 50%;
  display: none;
  content: "";
  transform: translate(-50%, 0);
  border: 0.625rem solid transparent;
  border-bottom: 1.0625rem solid #f5f6fa;
}

@media (max-width: 767px) {
  .head-nav__link.link-sub:before {
    display: none !important;
  }
}

.head-nav__link.link-sub.bottom-of-fixed:before {
  bottom: -2.125rem;
}

.head-nav__link.link-sub.active:before {
  display: block;
}

.head-nav__link.active:after {
  bottom: -0.625rem;
  width: 1.1875rem;
  height: 1.1875rem;
  background: url("../img/icons/cross.svg") no-repeat 50%/1.1875rem;
}

.sub-nav {
  position: absolute;
  z-index: 2;
  top: 9.875rem;
  right: 0;
  left: 0;
  display: none;
  background-color: #f5f6fa;
}

@media (max-width: 767px) {
  .sub-nav {
    position: static;
  }
}

.sub-nav.active {
  display: block;
}

.sub-nav__container {
  margin: 0 auto;
}

.sub-nav__cover {
  display: flex;
  width: 100%;
  padding: 3.4375rem 0;
  padding-left: 6.25rem;
  justify-content: center;
}

@media (max-width: 767px) {
